import React,{ useState } from "react";
import { Formik, Field, Form } from 'formik';
import AddTaskStep from "./AddTaskStep";
import "./CreateTaskForVolunteer.scss"

const CreateTaskForPartner = ({localization}) =>{

    const handleSubmit = (e) => {
        e.preventDefault();
        alert()
    }
    const [textAreaCount, ChangeTextAreaCount] = useState(0);
    const [inputValue, setInputValue] = useState('')
    const recalculate = e => {
        ChangeTextAreaCount(e.target.value.length);
        setInputValue(e.target.value)

    };
    const [Second,CountSecond] = useState(0);
    const [inputSecond, setInputSecond] = useState('')
    const recSecond = e =>{
        CountSecond(e.target.value.length);
        setInputSecond(e.target.value)
    }
    const [Third,CountThird] = useState(0);
    const [inputThird, setInputThird] = useState('')
    const recThird = e =>{
        CountThird(e.target.value.length);
        setInputThird(e.target.value)
    }
    const [fourth,CountFourth] = useState(0);
    const [inputFourth, setInputFourth] = useState('')
    const recFourth = e =>{
        CountFourth(e.target.value.length);
        setInputFourth(e.target.value)
    }
    const [fith,CountFith] = useState(0);
    const [inputFith, setInputFith] = useState('')
    const recFith = e =>{
        CountFith(e.target.value.length);
        setInputFith(e.target.value)
    }
    return(
    <Formik initialValues={{
        taskName:'',
        taskType:'',
        expireDate:'',
        taskDescription:'',
        resultDescription:'',
        volunteerReward:'',
        VolunteerSkills:'',
        saveMoney:'',
        currency:'',
        stageName:'',
        duration:'',
        stageDescription:'',

    }} onSubmit={handleSubmit}>
        <div className="container">
                <div className="title">
                <h3>{localization.task.register.partner}</h3>
                    <div className="title_subtitle">
                <h5>{localization.task.register.volunteerDescription}</h5>
                    </div>
                </div>
            <Form >
                <div className="taskName">
                    <label  className="taskName_title" htmlFor="taskName">{localization.task.name} </label>
                    <div className="taskName_text">{localization.task.nameDescription}</div>

                    <Field
                        as="textarea"
                        resize="none"
                        className = "taskName_input"
                        type="text"
                        name="taskName"
                        id="taskName"
                        rows={5}
                        maxLength="90"
                        value={inputValue}
                        onChange={recalculate}
                    />
                    <div className="taskName_areaCount_90">{textAreaCount}/90</div>
                </div>
                <div className="taskType">
                     <label className="taskType_title" htmlFor="taskType">{localization.task.category}:</label>
                      <Field
                          className="taskType_input"
                          list="type"
                          id="taskType"
                          name="taskType"
                          placeholder="  " />

                      <datalist id="type">
                          <option value=" " />
                          <option value=" " />
                          <option value=" " />
                          <option value=" 1" />
                          <option value=" 2" />
                          <option value=" 3" />
                      </datalist>
                     <div className="taskType_description"> {localization.task.categoryDescription} </div>
                </div>
                <div className="expireDateContainer">
                     <label className = "expireDate" htmlFor="expireDate"> {localization.task.deadline}:</label>
                        <Field
                            className = "expireDate_input"
                            id="expireDate"
                            name="expireDate"
                            placeholder=".."/>
                       <div className = "expireDate_description">{localization.task.volunteer.deadline.description}</div>
                </div>
                <div className="taskDescription">
                    <label className="taskDescription_title" htmlFor="taskDescription">{localization.task.about}:</label>
                    <Field
                        className='taskDescription_title_input'
                        type="text"
                        name="taskDescription"
                        id="taskDescription"
                        as="textarea"
                        resize="none"
                        rows={5}
                        maxLength="500"
                        value={inputSecond}
                        onChange={recSecond }
                    />
                    <div className="taskName_areaCount_500">{Second}/500</div>
                    <div className="taskDescription_description">{localization.task.aboutDescription}</div>
                </div>
                <div className="resultDescriptionContainer">
                    <label className="resultDescription_title" htmlFor="resultDescription">{localization.task.outcome}:</label>
                    <Field
                        className="resultDescription_input"
                        name="resultDescription"
                        id="resultDescription"
                        as="textarea"
                        resize="none"
                        rows={5}
                        maxLength="500"
                        value={inputThird}
                        onChange={recThird}
                    />
                    <div className="taskName_areaCount_500_2">{Third}/500</div>
                    <div className="resultDescription_description">{localization.task.outcomeDescription}</div>
                </div>
                <div className="volunteerRewardContainer">
                    <label  className="volunteerReward_title" htmlFor="volunteerReward">{localization.task.reward.volunteer}:</label>
                    <Field
                        className="volunteerReward_input"
                        type="text"
                        name="volunteerReward"
                        id="volunteerReward"
                    />
                    <div className="volunteerReward_description">{localization.task.reward.volunteer}</div>
                </div>
                <div className="volunteerSkillsContainer">
                    <label className="volunteerSkills_title" htmlFor="volunteerSkills">{localization.task.requirement.volunteer}:</label>
                    <Field
                        className="volunteerSkills_input_one"
                        type="text"
                        name="volunteerSkills"
                        id="volunteerSkills"
                        rows={5}
                        maxLength="50"
                        value={inputFourth}
                        onChange={recFourth}
                    />
                    <div className="taskName_areaCount_50_1">{fourth}/50</div>
                    <Field
                        className="volunteerSkills_input_two"
                        type="text"
                        name="volunteerSkills"
                        id="volunteerSkills"
                        rows={5}
                        maxLength="50"
                        value={inputFith}
                        onChange={recFith}
                    />
                    <div className="taskName_areaCount_50_2">{fith}/50</div>
                    <div className="volunteerSkills_description">{localization.task.requirement.volunteerDescription}</div>
                </div>
                <div className="saveMoneyContainer">
                    <label className="saveMoney_title" htmlFor="saveMoney">{localization.task.saved.amount}:</label>
                    <Field
                        className="saveMoney_input"
                        type="text"
                        name="saveMoney"
                        id="saveMoney"/>
                    <Field
                        className="saveMoney_currency"
                        list="symbol"
                        id="currency"
                        name="currency"
                        placeholder=" $" />
                    <datalist id="symbol">
                        <option  value="$" />
                        <option value="" />
                        <option value="" />
                    </datalist>
                    <div className="saveMoney_description">{localization.task.saved.amountDescription}</div>
                </div>
                <div className="stagesContainer">
                    <div className="stageForm_text">
                        <div className="stages"> {localization.task.stages}:</div>
                        <div className="stages_mainDescription">{localization.task.stagesMainDesc}</div>
                        <div className="stage_SecondaryDesc">{localization.task.stageSecondaryDesc}</div>
                    </div>

                <div className="stageForm_fields">
                    <div className="stageOne">{localization.stage} 1</div>
                        <label className="stageName_title" htmlFor="stageName">{localization.stageName}:</label>
                    <Field
                        className="stageName_input"
                        type="text"
                        id="stageName"
                        name="stageName"
                        placeholder="  "
                    />
                    <label className="stageName_duration" htmlFor="duration">{localization.stage.duration}</label>
                    <div className="wordDuration">{localization.duration}</div>
                    <Field
                        className="stageDuration_input "
                        list="dura"
                        id="duration"
                        name="duration"

                    />
                    <datalist id="dura">
                        <option  value="" />
                        <option value="" />
                    </datalist>

                    <label className="stageName_description_title" htmlFor="stageDescription">{localization.stageAbout}:</label>
                    <Field  className="stageName_description_input" type="text"
                        id="stageDescription"
                        name="stageDescription"
                            as="textarea"
                            resize="none"
                            rows={5}
                            maxLength="500"
                            value={inputValue}
                            onChange={recalculate}

                    />
                    <div className="taskName_areaCount_500_3">{textAreaCount}/500</div>
                    </div>
                </div>
                <AddTaskStep localization={localization}/>

            </Form>
            <button  className='button_createTask'>{localization.task.create}</button>
        </div>


    </Formik>

    )

}
export default CreateTaskForPartner
