import React from 'react'
import AboutProjectNews from './AboutProjectNews.js'
import './AboutProjectNews.scss'

function AboutNews({localization,locale}){
    return(
        <div className="about">
            <div className="about__project">
                <AboutProjectNews localization={localization} locale={locale}></AboutProjectNews>
            </div>
        </div>
    )
}

export default AboutNews