import React,{useState} from "react";
import { Formik, Field, Form, FieldArray } from 'formik';
import "./AddTaskStep.scss"

const initialValues = {
    fields: [
        {
            stageName:'',
            duration:'',
            stageDescription:'',
        },
    ],
};


const AddTaskStep = ({localization}) => {
    const [textAreaCount, ChangeTextAreaCount] = useState(0);
    const [inputValue, setInputValue] = useState('')
    const recalculate = e => {
        ChangeTextAreaCount(e.target.value.length);
        setInputValue(e.target.value)

    };


    return(
    <div className="wrapper">

        <Formik
            initialValues={initialValues}>
            {({values}) => (
                <Form>
                    <FieldArray name="fields">
                        {({push}) => (
                            <div className="container">
                                {values.fields.length > 0 &&
                                values.fields.map((field, index) => (


                                    <div key={index} className="activeForm">
                                        <div className="empty"></div>
                                        <div className="stage">{localization.stage} </div>
                                        <label className="stageName"
                                               htmlFor={`stageName${index}`}>{localization.stageName}:</label>
                                        <Field
                                            className="inputOne"
                                            type="text"
                                            id="stageName"
                                            name={`stageName.${index}`}
                                            placeholder="  "
                                        />
                                        <label className="duration"
                                               htmlFor={`duration.${index}`}>{localization.stage.duration}</label>
                                        <div className="WordDuration">{localization.duration}</div>
                                        <Field
                                            className="inputSecond"
                                            list="dura"
                                            id="duration"
                                            name={`duration.${index}`}
                                            placeholder=""
                                        />
                                        <datalist id="dura">
                                            <option value=""/>
                                            <option value=""/>
                                        </datalist>
                                        <label className="aboutStage_title"
                                               htmlFor={`stageDescription.${index}`}>{localization.stageAbout}:</label>
                                        <Field
                                            className="aboutStage_input"
                                            type="text"
                                            id="stageDescription"
                                            name={`stageDescription.${index}`}
                                            as="textarea"
                                            resize="none"
                                            rows={5}
                                            maxlength="500"
                                            value={inputValue}
                                            onChange={recalculate}
                                        />
                                        <div className="taskName_areaCount_500_3">{textAreaCount}/500</div>
                                    </div>
                                ))}
                                <button
                                    className="button_addStage"
                                    type="button"
                                    onClick={() => push({
                                        stageName: '',
                                        duration: '',
                                        stageDescription: '',
                                    })}
                                >
                                     
                                </button>
                            </div>
                        )}
                    </FieldArray>
                </Form>
            )}
        </Formik>
    </div>
    )
};
export default AddTaskStep;
