import React from 'react'
import Advice from './Advice'
import './Advice.scss'

function AdviceContainer({localization,locale}){
    return(
        <div className="about">
            <div className="about__project">
                <Advice localization={localization} locale={locale}></Advice>
            </div>
        </div>
    )
}

export default AdviceContainer