import React, {Component} from "react";
import './ErrorPage.scss'
import puzzle from './puzzle.png'

class ErrorPage extends Component {

    constructor(props) {
        super();
        this.state = {hasError: false}
    }

    static getDerivedStateFromError() {
        return {hasError: true};
    }

    componentDidCatch(error, info) {
        console.log('Cought an error', error, info);

    }

    render() {
        if (this.state.hasError) {
            return <div className='errorPage container'>
                <div className="errorPage__messageContainer">
                    <div className="errorPage__messages">
                        <p className="errorPage__line">{this.props.localization.error.firstLine}</p>
                        <p className="errorPage__line">{this.props.localization.error.secondLine}</p>
                        <p id="testIt" className="errorPage__message">{this.props.localization.error.message}</p>
                    </div>
                    <div className="errorPage__buttonsContainer">
                        <a href="/">
                            <button
                                className='button static_button button--orange'>{this.props.localization.error.firstButton}</button>
                        </a>
                        <button
                            className='button static_button button--orange'>{this.props.localization.error.secondButton}</button>
                    </div>
                </div>
                <div className='errorPage__puzzleContainer'>
                    <img src={puzzle} alt='puzzle'/>
                </div>
            </div>
        }
        return this.props.children;
    }
}

export default ErrorPage;
