import React, {useState, useEffect} from 'react'
import {
    HashRouter as Router,
    Switch,
    Route
} from "react-router-dom"
import Header from './Header/Header'
import MainPage from './MainPage/MainPage'
import Footer from './Footer/Footer'
import RegistrationPage from './RegistrationPage/RegistrationPage'
import VolunteerRegistration from './Registration/VolunteerRegistration'
import OrganizationRegistration from './Registration/OrganizationRegistration'
import PartnerRegistration from './Registration/PartnerRegistration'
import Confirm from './Registration/Confirm'
import LoginPage from './LoginPage/LoginPage'
import About from "./About/AboutProject/AboutProjectContainer";
import AboutNews from "./About/AboutProjectNews/AboutProjectNewsContainer";
import CreateTaskForVolunteer from './MainPage/TaskCreator/CreateTaskForVolunteer'
import CreateTaskForPartner from './MainPage/TaskCreator/CreateTaskForPartner'
import EditTaskForVolunteer from './MainPage/TaskCreator/EditTaskForVolunteer'
import './App.scss'
import './CustomBootstrap.scss'
import lang from './language'
import ErrorPage from "./ErrorPage/ErrorPage";
import RulesContainer from "./About/Rules/RulesContainer";
import HelpContainer from "./About/Help/HelpContainer";
import AdviceContainer from "./About/Advice/AdviceContainer";
import EditPersonalData from "./PersonalAccount/PersonalData/EditPersonalData";


function App() {
    const [language, setLanguage] = useState(lang.RU);
    const [locale, setLocale] = useState('RU')

    useEffect(() => {
        const current = localStorage.getItem('language');
        if (current === null) {
            setLanguage(lang.RU);
            setLocale('RU');
        } else if (current === 'EN') {
            setLanguage(lang.EN);
            setLocale('EN');
        } else if (current === 'RU') {
            setLanguage(lang.RU);
            setLocale('RU')
        } else if (current === 'UA') {
            setLanguage(lang.UA);
            setLocale('UA');
        }
    }, [])

    return (
        <Router>

            <Header language={language} onLanguageChange={setLanguage} setLocale={setLocale}
                    localization={language}></Header>
            <ErrorPage localization={language}>
                <Switch>
                    <Route path="/create-task-vol">
                        <CreateTaskForVolunteer localization={language}></CreateTaskForVolunteer>
                    </Route>
                    <Route path="/create-task-partner">
                        <CreateTaskForPartner localization={language}></CreateTaskForPartner>
                    </Route>
                    <Route path="/edit-task-vol">
                        <EditTaskForVolunteer localization={language}></EditTaskForVolunteer>
                    </Route>

                    <Route path="/registration/confirm/:token">
                        <Confirm localization={language}></Confirm>
                    </Route>
                    <Route path="/volunteer-registration">
                        <VolunteerRegistration localization={language}></VolunteerRegistration>
                    </Route>
                    <Route path="/organization-registration">
                        <OrganizationRegistration localization={language}></OrganizationRegistration>
                    </Route>
                    <Route path="/partner-registration">
                        <PartnerRegistration localization={language}></PartnerRegistration>
                    </Route>
                    <Route path="/registration-page">
                        <RegistrationPage localization={language}></RegistrationPage>
                    </Route>
                    <Route path="/login-page">
                        <LoginPage localization={language}></LoginPage>
                    </Route>
                    <Route path="/about">
                        <About localization={language} locale={locale}></About>
                    </Route>
                    <Route path="/news">
                        <AboutNews localization={language} locale={locale}></AboutNews>
                    </Route>
                    <Route path="/rules">
                        <RulesContainer localization={language} locale={locale}></RulesContainer>
                    </Route>
                    <Route path="/help">
                        <HelpContainer localization={language} locale={locale}></HelpContainer>
                    </Route>
                    <Route path="/advice">
                        <AdviceContainer localization={language} locale={locale}></AdviceContainer>
                    </Route>
                    <Route path="/partner-personal-account">
                        <EditPersonalData localization={language}></EditPersonalData>
                    </Route>
                    <Route path="/">
                        <MainPage localization={language}></MainPage>
                    </Route>
                </Switch>
            </ErrorPage>
            <Footer localization={language}></Footer>
        </Router>
    )

}

export default App